import tabs from "../../components/common/tabs/tabs.vue";
export default {
    components:{
        tabs
    },
       created() {
           //计算滚动高度
           uni.getSystemInfo({
               success: function (res) {
                   this.windowHeight=res.windowHeight;
               }
           });
           // 请求订单数据
           this.getData()
       },
       data() {
           return {
               taskType:0,
               dyOrderList: [],
               orderList: [],
               // 滚动条高度
               windowHeight:0
           };
       },
       methods: {
           // tabs 监听
           changeTabIndex(index){
               // 0 抖音  1 普通任务
               this.taskType=index;
           },
           async getData() {
               await this.$u.get('/my_dy_task_order').then(res => {
                   this.dyOrderList = res.data
               })
               await this.$u.get('/my_task_order').then(res => {
                   this.orderList = res.data
               })
           },
           submit(orderSn){               
             this.$u.route({
                 url:'pages/placeOrder/placeOrder',
                 params:{
                     orderSn
                 }
             })
           },
           async changeTaskOrderStatus(status,orderSn){
              await this.$u.post('/change_order_status',{status,orderSn}).then(res=>{
                   if(res.errorCode!==0)return;
                   uni.showToast({
                       title:res.msg
                   })
                   this.getData();
               })
           },
       }
   }
